.floor1 {
  .info {
    &__container {
      background: url('../../assets/pages/floor1/floor1-info.png') no-repeat
        top/ 100% 100%;
      &--lg {
        background: url('../../assets/pages/floor1/floor1-info-lg.png')
          no-repeat top/ 100% 100%;
      }
    }
    &__title {
      p {
        &::before {
          background-color: $blue;
        }
      }
    }
    &__subtitle {
      p {
        &::before {
          background-color: $blue;
        }
      }
    }
  }
  .visiter {
    width: em(170);
    height: em(250);
    top: em(600);
    right: 15%;
  }
  .floor {
    &__wrapper {
      background: url('../../assets/pages/floor1/floor1-bg.png') no-repeat right
        top/auto em(1080);
    }
    &__question {
      &::after {
        background: url('../../assets/pages/floor1/origin/floor1-bubble.png')
          no-repeat center/100%;
      }
      &--active,
      &:hover {
        &::after {
          background: url('../../assets/pages/floor1/active/floor1-bubble--active.png')
            no-repeat center/100%;
        }
      }
      &--1 {
        z-index: 9;
        top: em(600);
        right: 25%;
        img {
          width: em(240);
        }
      }
      &--2 {
        top: em(200);
        right: 22%;
        img {
          width: em(350);
        }
      }
      &--3 {
        top: em(350);
        right: 40%;
        img {
          width: em(180);
        }
        &::after {
          left: em(-150);
        }
      }
    }
  }
}

@media screen and (max-width: $laptop-width) {
  .floor1 {
    .visiter {
      right: 20%;
    }
    .floor {
      &__question {
        &--1 {
          right: 30%;
        }
        &--2 {
          right: 25%;
        }
        &--3 {
          right: 45%;
        }
      }
    }
  }
}
@media screen and (max-width: $desktop-width) {
  .floor1 {
    .info {
      &__container {
        background: url('../../assets/pages/floor1/floor1-info-m.png') no-repeat
          top/ 100%;
      }
    }
    .visiter {
      width: em(120);
      top: em(460);
      left: 70%;
    }
    .floor {
      &__wrapper {
        background: url('../../assets/pages/floor1/floor1-bg-m.png') no-repeat
          right em(150) / 130%;
      }
      &__question {
        &--1 {
          z-index: 1;
          top: em(460);
          left: 45%;
          img {
            width: em(150);
          }
        }
        &--2 {
          top: em(230);
          left: 55%;
          img {
            width: em(250);
          }
        }
        &--3 {
          top: em(280);
          left: 20%;
          img {
            width: em(120);
          }
          &::after {
            left: em(-100);
          }
        }
      }
    }
  }
}
