@charset "UTF-8";

.nav {
  z-index: 9999;
  position: fixed;
  right: 0;
  width: em(170);
  height: 100vh;
  margin: auto;
  &-container {
    z-index: 999;
    transition: all 1s;
    transform: translate(0%) !important;
  }
  &-popup {
    z-index: 9;
    position: fixed;
    top: 0;
    left: 0;
    height: 100vh;
    width: 100%;
    pointer-events: none;
    &::before {
      @include pseudo-element;
      opacity: 0;
      width: 100%;
      height: 100%;
      transition: 1s;
      background-color: $black;
    }
    &--active {
      &::before {
        opacity: 0.6;
      }
      .nav-popup__container {
        transform: translateX(0);
      }
    }
    &__container {
      position: absolute;
      display: flex;
      flex-direction: column;
      align-items: center;
      justify-content: center;
      top: 0;
      right: 0;
      width: em(365);
      height: 100%;
      transition: 1s;
      background-color: $gray;
      transform: translateX(110%);
      pointer-events: initial;
    }
    &__link {
      margin-bottom: em(64);
      p {
        display: inline-block;
        position: relative;
        z-index: 9;
        font-size: $f__20;
        line-height: 2;
        &::before {
          z-index: -1;
          @include pseudo-element;
          width: 0%;
          height: em(10);
          top: em(12);
          left: em(-5);
          background-color: $yellow;
          transition: all 1s;
        }
      }
      &--active,
      &:hover {
        p {
          &::before {
            width: calc(100% + em(10));
          }
        }
      }
    }
    &__logo {
      display: none;
    }
  }
  &-trigger {
    cursor: pointer;
    z-index: 99;
    position: relative;
    display: block;
    width: em(100);
    height: em(100);
    border-radius: 100%;
    border: em(4) solid $black;
    margin: em(56) 0 em(160);
    background-color: $white;
    transition: all 1s;
    span {
      position: absolute;
      display: block;
      width: em(50);
      height: em(5);
      border-radius: em(5);
      background-color: $black;
      transition: all 0.5s;
      &:first-of-type {
        top: 50%;
        left: 50%;
        transform: translate(-50%, em(-10));
      }
      &:nth-of-type(2) {
        top: 50%;
        left: 50%;
        transform: translate(-50%, em(10));
      }
    }
    &--active {
      transform: translateX(-52.5px);
      span {
        &:first-of-type {
          transform: translate(-50%, em(0)) rotate(20deg);
        }
        &:nth-of-type(2) {
          transform: translate(-50%, em(0)) rotate(-20deg);
        }
      }
    }
  }
  &__link {
    position: relative;
    display: block;
    text-align: left;
    padding: em(32) em(0) em(32) em(28);
    p {
      font-size: $f__18;
    }
    span {
      position: absolute;
      display: block;
      top: em(39);
      left: em(0);
      width: em(12);
      height: em(12);
      background: url('../../assets/buttons/btn-menu.png') no-repeat center/100%;
    }
    &--active {
      span {
        width: em(24);
        height: em(24);
        left: em(-6);
        background: url('../../assets/buttons/btn-menu-active.png') no-repeat
          center/100%;
      }
    }
  }
  &-social {
    position: absolute;
    bottom: em(120);
    display: flex;
    justify-content: center;
    &__icon {
      width: em(40);
      height: em(40);
      margin: 0 em(20);
    }
    .line {
      background: url('../../assets/icons/social/line.png') no-repeat
        center/100%;
    }
    .fb {
      background: url('../../assets/icons/social/fb.png') no-repeat center/100%;
    }
    .ig {
      background: url('../../assets/icons/social/ig.png') no-repeat center/100%;
    }
  }
}

@media screen and (max-width: $laptop-width) {
  .nav {
    font-size: 0.8em;
  }
}

@media screen and (max-width: $pad-width) {
  .nav {
    font-size: 0.6em;
  }
}

@media screen and (max-width: $desktop-width) {
  .nav {
    width: em(80);
    height: em(80);
    font-size: 1em;
    &-container {
      position: fixed;
      justify-content: center;
      left: 0;
      bottom: 0;
      width: 100%;
      display: flex;
      transform: translate(0%, 0%);
      background-color: $white;
      border-top: em(5) solid $black;
    }
    &-trigger {
      z-index: 9999;
      position: fixed;
      font-size: 0.6em;
      padding: em(30);
      &--active {
        transform: none;
      }
    }
    &-popup {
      z-index: 999;
      position: fixed;
      &__container {
        width: 100%;
      }
      &__link {
        font-size: $f__20;
        margin-bottom: em(40);
      }
      &--active {
      }
      .nav-popup__logo {
        display: block;
        position: absolute;
        z-index: 999;
        top: em(32);
        left: em(32);
        width: em(260);
      }
    }
    &__link {
      text-align: center;
      padding: em(50) em(32) em(14);
      span {
        top: em(16);
        left: 50%;
        transform: translateX(-50%) rotate(90deg);
      }
      &--active {
        span {
          top: em(8);
          background: url('../../assets/buttons/btn-menu-active.png') no-repeat
            center/100%;
        }
      }
    }
  }
}
