.floor3 {
  .info {
    &__container {
      background: url('../../assets/pages/floor3/floor3-info.png') no-repeat
        top/ 100% 100%;
      &--lg {
        padding-right: em(10);
        // height: em(500);
        background: url('../../assets/pages/floor3/floor3-info-lg.png')
          no-repeat top/ 100% 100%;
      }
    }
    &__title {
      p {
        &::before {
          background-color: $red;
        }
      }
    }
    &__subtitle {
      p {
        &::before {
          background-color: $red;
        }
      }
    }
  }
  .visiter {
    width: em(200);
    height: em(240);
    top: em(600);
    right: 30%;
    transform: translate(-160%, -20%);
  }
  .floor {
    &__wrapper {
      background: url('../../assets/pages/floor3/floor3-bg.png') no-repeat right
        top/auto em(1080);
    }
    &__question {
      &::after {
        background: url('../../assets/pages/floor3/origin/floor3-bubble.png')
          no-repeat center/100%;
      }
      &--active,
      &:hover {
        &::after {
          background: url('../../assets/pages/floor3/active/floor3-bubble--active.png')
            no-repeat center/100%;
        }
      }
      &--1 {
        top: em(320);
        right: 38%;
        img {
          width: em(160);
          &:nth-of-type(2) {
            width: em(160);
            left: 10%;
          }
        }
        &::after {
          top: em(-80);
          left: em(-30);
        }
      }
      &--2 {
        top: em(250);
        right: 13%;
        img {
          width: em(140);
          &:nth-of-type(2) {
            left: 15%;
          }
        }
        &::after {
          left: em(-50);
        }
      }
      &--3 {
        z-index: 9;
        top: em(550);
        right: 17%;
        img {
          width: em(220);
        }
        &::after {
          top: em(-50);
          left: em(-50);
        }
      }
    }
  }
}

@media screen and (max-width: $laptop-width) {
  .floor3 {
    .visiter {
      right: 32%;
    }
    .floor {
      &__question {
        &--1 {
          right: 40%;
        }
        &--2 {
          right: 14%;
        }
        &--3 {
          right: 18%;
        }
      }
    }
  }
}
@media screen and (max-width: $desktop-width) {
  .floor3 {
    .info {
      &__container {
        background: url('../../assets/pages/floor3/floor3-info-m.png') no-repeat
          top/ 100%;
        &--lg {
          height: em(1100);
        }
      }
    }
    .visiter {
      top: em(400);
      left: 50%;
      width: em(150);
    }
    .floor {
      &__wrapper {
        background: url('../../assets/pages/floor3/floor3-bg-m.png') no-repeat
          right em(150) / 130%;
      }
      &__question {
        &--3 {
          top: em(380);
          left: 60%;
          img {
            width: em(160);
          }
          &::after {
            top: em(0);
            left: em(0);
          }
        }
        &--2 {
          top: em(200);
          left: 68%;
          img {
            width: em(100);
            &:nth-of-type(2) {
              width: em(100);
              left: 20%;
            }
          }
          &::after {
            top: em(0);
            left: em(0);
          }
        }
        &--1 {
          top: em(240);
          left: 20%;
          img {
            width: em(120);
            &:nth-of-type(2) {
              width: em(100);
              left: 25%;
              top: 10%;
            }
          }
          &::after {
            top: em(0);
            left: em(0);
          }
        }
      }
    }
  }
}
