.visiter {
  cursor: pointer;
  position: absolute;
  display: block;
  width: em(170);
  height: em(250);
  transition: all 1s;
  img {
    width: 100%;
    position: absolute;
    top: 0;
    left: 0;
    opacity: 0;
    &:first-of-type {
      opacity: 1;
    }
  }
  &--goLeft {
    img {
      &:nth-of-type(1) {
        opacity: 0;
      }
      &:nth-of-type(2) {
        animation: toHide2 0.2s alternate-reverse infinite;
      }
      &:nth-of-type(3) {
        animation: toShow2 0.2s alternate-reverse infinite;
      }
    }
  }
  &--goRight {
    img {
      &:nth-of-type(1) {
        opacity: 0;
      }
      &:nth-of-type(5) {
        animation: toHide2 0.2s alternate-reverse infinite;
      }
      &:nth-of-type(6) {
        animation: toShow2 0.2s alternate-reverse infinite;
      }
    }
  }
  &--left {
    img {
      &:nth-of-type(1) {
        opacity: 1;
      }
    }
  }
  &--right {
    img {
      &:nth-of-type(1) {
        opacity: 0;
      }
      &:nth-of-type(4) {
        opacity: 1;
      }
    }
  }
}
@media screen and (max-width: $desktop-width) {
  .visiter {
    width: em(120);
  }
}
