.floor2 {
  .info {
    &__container {
      background: url('../../assets/pages/floor2/floor2-info.png') no-repeat
        top/ 100% 100%;
      &--lg {
        // height: em(500);
        background: url('../../assets/pages/floor2/floor2-info-lg.png')
          no-repeat top/ 100% 100%;
      }
    }
    &__title {
      p {
        &::before {
          background-color: $purple;
        }
      }
    }
    &__subtitle {
      p {
        &::before {
          background-color: $purple;
        }
      }
    }
  }
  .visiter {
    width: em(190);
    height: em(250);
    top: em(320);
    right: 15%;
    transform: translate(-20%, -20%);
  }
  .floor {
    &__wrapper {
      background: url('../../assets/pages/floor2/floor2-bg.png') no-repeat right
        top/auto em(1080);
    }
    &__question {
      &::after {
        background: url('../../assets/pages/floor2/origin/floor2-bubble.png')
          no-repeat center/100%;
      }
      &--active,
      &:hover {
        &::after {
          background: url('../../assets/pages/floor2/active/floor2-bubble--active.png')
            no-repeat center/100%;
        }
      }
      &--1 {
        z-index: 9;
        top: em(500);
        right: 23%;
        img {
          width: em(350);
        }
      }
      &--2 {
        top: em(200);
        right: 30%;
        img {
          width: em(220);
        }
      }
      &--3 {
        top: em(400);
        right: 55%;
        img {
          width: em(260);
        }
      }
    }
  }
}

@media screen and (max-width: $laptop-width) {
  .floor2 {
    .visiter {
      right: 13%;
    }
    .floor {
      &__question {
        &--1 {
          right: 25%;
        }
        &--2 {
          right: 32%;
        }
        &--3 {
          right: 60%;
        }
      }
    }
  }
}
@media screen and (max-width: $desktop-width) {
  .floor2 {
    .info {
      &__container {
        background: url('../../assets/pages/floor2/floor2-info-m.png') no-repeat
          top/ 100%;
        &--lg {
          height: em(1100);
        }
      }
    }
    .visiter {
      top: em(350);
      left: 75%;
      width: em(150);
    }
    .floor {
      &__wrapper {
        background: url('../../assets/pages/floor2/floor2-bg-m.png') no-repeat
          right em(150) / 130%;
      }
      &__question {
        &--1 {
          z-index: 9;
          top: em(360);
          left: 50%;
          img {
            width: em(250);
          }
        }
        &--2 {
          top: em(150);
          left: 30%;
          img {
            width: em(180);
          }
        }
        &--3 {
          top: em(280);
          left: -10%;
          img {
            width: em(200);
          }
        }
      }
    }
  }
}
