.fade-enter-active,
.fade-leave-active {
  transition: all 1s;
  transition: opacity 0.8s !important;
}
.fade-enter,
.fade-enter-from,
.fade-leave-to {
  transition: all 1s;
  opacity: 0 !important;
}
.fade-enter-from,
.fade-leave-to {
  transition: all 1s;
}
.fade-enter-to,
.fade-leave-from {
  transition: all 1s;
  transform: translateY(0%);
}

@keyframes showTop {
  0% {
    opacity: 0;
    transform: translateY(20%);
  }

  100% {
    opacity: 1;
    transform: translateY(0);
  }
}
@keyframes showDown {
  0% {
    opacity: 0;
    transform: translateY(-20%);
  }
  100% {
    opacity: 1;
    transform: translateY(0);
  }
}

@keyframes hideTop {
  0% {
    opacity: 1;
    transform: translateY(20%);
  }
  100% {
    opacity: 0;
    transform: translateY(0);
  }
}
@keyframes hideDown {
  0% {
    opacity: 1;
    transform: translateY(-20%);
  }
  100% {
    opacity: 0;
    transform: translateY(0);
  }
}
@keyframes toShow {
  0% {
    opacity: 0;
  }
  100% {
    opacity: 1;
  }
}

@keyframes toHide {
  0% {
    opacity: 1;
  }
  100% {
    opacity: 0;
  }
}
