.lobby {
  position: relative;
  display: flex;
  &__wrapper {
    width: calc(100% - em(170));
    height: 100%;
    max-width: em(1750);
    background: url('../../assets/pages/lobby/lobby-bg.png') no-repeat right
      top/auto em(1080);
  }
  &__title {
    position: absolute;
    left: em(5);
    bottom: em(-44);
    // bottom: em(30);
    width: em(420);
    height: em(130);
    img {
      width: 100%;
    }
  }
  .info {
    z-index: 999;
    position: absolute;
    left: em(80);
    top: em(120);
    width: 29vw;
    height: 43vw;
    border-radius: em(32);
    padding: em(140) em(48) 0 em(48);
    background: url('../../assets/frames/info-basic.png') no-repeat center/ 100%;
    &__scroll {
      display: none;
    }
    &__container {
      max-height: 80%;
      text-align: left;
      overflow: hidden;
    }
    &__index {
      p {
        font-size: $f__16;
        font-weight: bold;
      }
    }
    &__title {
      margin: em(8) 0 em(24);
      span {
        position: relative;
        font-size: $f__24;
        z-index: 9;
        line-height: 2;
        font-weight: bold;
      }
      p {
        display: inline-block;
        position: relative;
        &::before {
          z-index: 0;
          @include pseudo-element;
          width: 0%;
          height: em(10);
          top: em(20);
          left: em(0);
          background-color: $green;
          transition: all 1s;
        }
        &:hover,
        &.active {
          &::before {
            width: 100%;
          }
        }
      }
    }
    &__content {
      padding-right: em(20);
      p {
        font-size: $f__16;
        line-height: 2;
      }
    }
    &__next,
    &__prev {
      display: flex;
      align-items: center;
      position: absolute;
      @include flex-jcc-atc;
      bottom: em(60);
      p {
        font-size: $f__16;
        line-height: 2;
      }
      img {
        width: em(15);
      }
    }
    &__next {
      right: em(60);
      p {
        margin-right: em(16);
      }
    }
    &__prev {
      left: em(60);
      p {
        margin-left: em(16);
      }
    }
  }
  &__question {
    cursor: pointer;
    position: absolute;
    display: block;
    width: em(200);
    height: em(200);
    img {
      width: 100%;
      transition: all 0.5s;
      &:nth-of-type(2) {
        position: absolute;
        top: 0;
        left: 0;
        opacity: 0;
      }
    }
    &--1,
    &--2,
    &--3,
    &--4 {
      &::after {
        @include pseudo-element;
        top: em(-60);
        left: em(-20);
        width: em(80);
        height: em(80);
        background: url('../../assets/pages/lobby/origin/lobby-bubble.png')
          no-repeat center/100%;
        animation: bubbling 1s alternate linear infinite;
      }
    }
    &--active,
    &:hover {
      img {
        &:nth-of-type(1) {
          opacity: 0;
        }
        &:nth-of-type(2) {
          opacity: 1;
        }
      }
      &::after {
        background: url('../../assets/pages/lobby/active/lobby-bubble--active.png')
          no-repeat center/100%;
      }
    }
    &--action {
      img {
        transition: opacity 1s;
        &:nth-of-type(1) {
          animation: toHide2 0.8s alternate-reverse infinite;
        }
        &:nth-of-type(2) {
          animation: toShow2 0.8s alternate-reverse infinite;
        }
      }
    }

    &--1 {
      top: em(550);
      right: 55%;
      img {
        width: em(180);
        &:nth-of-type(2) {
          left: 5%;
        }
      }
    }
    &--2 {
      top: em(360);
      // right: 44%;
      right: 36%;
      img {
        width: em(170);
        &:nth-of-type(2) {
          left: 7%;
        }
      }
    }
    &--3 {
      top: em(250);
      right: 15%;
      &::after {
        top: em(-10);
        right: em(-20);
      }
    }
    &--4 {
      z-index: 9;
      top: em(560);
      // right: 22%;
      right: 19%;
      img {
        width: em(260);
      }
      &::after {
        top: em(-50);
        left: em(-50);
      }
    }
    &--5 {
      top: em(550);
      right: 30%;
    }
    &--6 {
      top: em(600);
      right: 45%;
      width: em(140);
    }
    &--7 {
      top: em(320);
      right: 25%;
      width: em(135);
    }
  }
}

@media screen and (max-width: $laptop-width) {
  .lobby {
    font-size: 0.8em;
    .info {
      font-size: 0.9em;
    }
    &__question {
      &--1 {
        right: 70%;
      }
      &--2 {
        right: 43%;
      }
      &--3 {
        right: 18%;
      }
      &--5 {
        right: 35%;
      }
      &--6 {
        right: 55%;
      }
      &--7 {
        right: 30%;
      }
    }
  }
}

@media screen and (max-width: $pad-width) {
  .lobby {
    font-size: 0.6em;
  }
}
@media screen and (max-width: $desktop-width) {
  .lobby {
    font-size: 1em;
    overflow: hidden;
    &__wrapper {
      width: 100%;
      background: url('../../assets/pages/lobby/lobby-bg-m.png') no-repeat right
        em(100) / 100%;
    }
    .info {
      left: 50%;
      top: em(750);
      width: 90vw;
      height: 160vw;
      border-radius: em(32);
      padding: em(200) em(30) em(150) em(30);
      transform: translateX(-50%);
      background: url('../../assets/frames/info-basic-m.png') no-repeat center/
        100%;
      &__scroll {
        position: absolute;
        top: 0;
        left: em(0);
        display: flex;
        align-items: center;
        justify-content: flex-start;
        img {
          width: em(30);
        }
      }
      &__container {
        overflow: scroll;
        max-height: 95%;
      }
      &__title {
        span {
          font-size: $f__36;
          line-height: 1.5;
        }
      }
      &__content {
        font-size: $f__26;
        padding-right: em(10);
      }
      &__next,
      &__prev {
        p {
          font-size: $f__28;
          font-weight: bold;
        }
        img {
          width: em(20);
        }
      }
    }
    &__question {
      img {
        width: 80%;
      }
      &--1 {
        top: em(320);
        left: 0%;
        img {
          width: em(140);
        }
      }
      &--2 {
        top: em(200);
        left: 40%;
        img {
          width: em(130);
        }
      }
      &--3 {
        top: em(110);
        right: -12%;
        img {
          width: em(160);
        }
      }
      &--4 {
        top: em(350);
        right: -3%;
        img {
          width: em(200);
        }
        &::after {
          left: em(-80);
        }
      }
      &--5 {
        top: em(320);
        left: 45%;
      }
      &--6 {
        top: em(350);
        left: 30%;
      }
      &--7 {
        top: em(200);
        left: 70%;
      }
    }
  }
}
