.logo {
  z-index: 999;
  position: absolute;
  width: 8.85vw;
  top: 0;
  left: 0;
  transition: all 1s;
  transform-origin: 0 0;
  img {
    width: 100%;
  }
  &--home {
    top: 7.5vh;
    left: 5.4vw;
    width: 23.7vw;
  }
}

@media screen and (max-width: $laptop-width) {
  .logo {
    // top: em(40);
    // left: em(40);
    // transform: scale(0.8);
  }
}

@media screen and (max-width: $pad-width) {
  .logo {
    // top: em(20);
    // left: em(25);
    // transform: scale(0.6);
  }
}
@media screen and (max-width: $desktop-width) {
  .logo {
    width: 32vw;
    transform: scale(1);
    &--home {
      top: 1.5em;
      left: 1.5em;
    }
  }
}
