.floor {
  position: relative;
  display: flex;
  &__wrapper {
    width: calc(100% - em(170));
    height: 100%;
    max-width: em(1750);
  }
  &__title {
    position: absolute;
    left: em(5);
    // bottom: em(44);
    bottom: em(-33);
    width: em(420);
    height: em(100);
    img {
      width: 100%;
    }
  }
  .info {
    z-index: 999;
    position: absolute;
    left: em(80);
    top: em(180);
    width: em(590);
    &__scroll {
      display: none;
    }
    &__container {
      position: relative;
      // height: em(380);
      height: auto;
      text-align: left;
      margin: em(24) 0;
      padding: em(32) em(40) em(120) em(180);
      &--lg {
        // height: em(450);
      }
    }
    &__index {
      p {
        font-size: $f__16;
        font-weight: bold;
      }
    }
    &__title {
      margin: 0;
      margin-left: em(20);
      img {
        width: em(204);
      }
      span {
        position: relative;
        font-size: $f__24;
        z-index: 9;
        line-height: 2;
        font-weight: bold;
      }
      p {
        display: inline-block;
        position: relative;
        &::before {
          z-index: 0;
          @include pseudo-element;
          width: 0%;
          height: em(16);
          top: em(30);
          left: em(-10);
          transition: all 1s;
          background-color: $blue;
        }
        &:hover,
        &.active {
          &::before {
            width: 110%;
          }
        }
        span {
          font-size: $f__34;
        }
      }
    }
    &__text {
      margin-left: em(20);
    }
    &__content {
      padding-right: em(20);
      p {
        font-size: $f__16;
        line-height: 2;
      }
    }
    &__cta {
      position: absolute;
      @include flex-jcc-atc;
      right: em(60);
      bottom: em(60);
      p {
        font-size: $f__16;
        margin-right: em(16);
      }
      img {
        width: em(10);
      }
    }
    &__card {
      cursor: pointer;
      position: absolute;
      top: em(32);
      left: em(-20);
      width: em(176);
      height: em(276);
      padding-top: em(40);
      text-align: center;
      background-color: $white;
      border-radius: em(8);
      border: em(5) solid $black;
      transform: rotate(-5deg);
      transform-origin: 50% em(10);
      animation: cardShake 0.5s alternate linear forwards;
      img {
        width: em(144);
        height: em(144);
        margin-bottom: em(10);
      }
      &::after {
        @include pseudo-element;
        top: em(-30);
        left: 40%;
        width: em(60);
        height: em(60);
        background: url('../../assets/icons/Pin.png') no-repeat center/100%;
      }
      &:hover {
        animation: cardShake 1s alternate linear;
      }
      &--active {
        transform: rotate(0);
        top: em(32);
        padding-top: em(0);
        left: 0;
        border: 0;
        background-color: transparent;
        &::after {
          content: none;
        }
      }
    }
    &__next,
    &__prev {
      display: flex;
      align-items: center;
      position: absolute;
      @include flex-jcc-atc;
      bottom: em(60);
      p {
        font-size: $f__16;
        line-height: 2;
      }
      img {
        width: em(15);
      }
    }
    &__next {
      right: em(60);
      p {
        margin-right: em(16);
      }
    }
    &__prev {
      left: em(60);
      p {
        margin-left: em(16);
      }
    }
    &__subtitle {
      span {
        position: relative;
        font-size: $f__24;
        z-index: 9;
        line-height: 2;
        font-weight: bold;
      }
      p {
        display: inline-block;
        position: relative;
        &::before {
          z-index: 0;
          @include pseudo-element;
          width: 0%;
          height: em(16);
          top: em(24);
          left: em(-10);
          background-color: $blue;
          transition: all 1s;
        }
        &:hover,
        &.active {
          &::before {
            width: 110%;
          }
        }
      }
    }
  }
  &__question {
    cursor: pointer;
    position: absolute;
    display: block;
    width: em(200);
    height: em(200);
    img {
      width: 100%;
      transition: all 0.5s;
      &:nth-of-type(2) {
        position: absolute;
        top: 0;
        left: 0;
        opacity: 0;
      }
    }
    &::after {
      @include pseudo-element;
      top: 0;
      width: em(80);
      height: em(80);
      animation: bubbling 1s alternate linear infinite;
    }
    &--active,
    &:hover {
      img {
        &:nth-of-type(1) {
          opacity: 0;
        }
        &:nth-of-type(2) {
          opacity: 1;
        }
      }
    }
  }
}

@media screen and (max-width: $laptop-width) {
  .floor {
    font-size: 0.8em;
    .info {
      font-size: 0.9em;
    }
  }
}

@media screen and (max-width: $pad-width) {
  .floor {
    font-size: 0.6em;
  }
}
@media screen and (max-width: $desktop-width) {
  .floor {
    font-size: 1em;
    overflow: hidden;
    &__wrapper {
      width: 100%;
    }
    &__question {
      &::after {
        width: em(50);
        height: em(50);
      }
      img {
        width: 95%;
      }
    }
    .info {
      left: 50%;
      top: em(800);
      width: 90%;
      transform: translateX(-50%);
      &__scroll {
        display: flex;
        align-items: center;
        justify-content: flex-start;
        img {
          width: em(30);
        }
      }
      &__title {
        margin-left: 0;
        img {
          width: em(240);
        }
        p {
          span {
            line-height: 1.2;
            font-size: em(56);
          }
          &::before {
            left: em(-10);
            top: em(40);
            height: em(26);
          }
        }
        span {
          font-size: em(36);
        }
      }
      &__text {
        margin-left: 0;
        font-size: em(28);
      }
      &__container {
        height: em(1100);
        padding: em(250) em(40) em(32) em(40);
      }
      &__content {
        font-size: em(28);
      }
      &__card {
        width: em(450);
        height: em(200);
        left: 15%;
        display: flex;
        align-items: center;
        flex-direction: row;
        padding: em(20);
        padding-top: em(20);
        div {
          margin-left: em(20);
          text-align: left;
          font-size: 1.3em;
        }
        &--active {
          top: em(16);
          left: em(24);
          width: 90%;
          height: em(148);
          padding: 0;
          border-radius: 0;
          border-bottom: em(5) solid $black;
          div {
            p {
              margin: em(8) 0;
            }
          }
          img {
            width: em(100);
            height: em(100);
          }
        }
      }
      &__next,
      &__prev {
        bottom: em(120);
        p {
          font-size: $f__28;
          font-weight: bold;
        }
        img {
          width: em(20);
        }
      }
    }
  }
}
