.footer {
  cursor: pointer;
  display: flex;
  justify-content: center;
  position: fixed;
  padding: em(5) 0;
  bottom: 0;
  width: 100vw;
  background-color: $gray;
  border-top: 3px solid $black;
  transition: all 1s;
  transform: translateY(0);
  span {
    position: absolute;
    top: em(-50);
    display: block;
    width: 100%;
    height: em(100);
  }
  p {
    font-size: 12px;
    line-height: 1.5;
  }
  a {
    &:link,
    &:active,
    &:visited {
      line-height: 0;
      color: $black;
      border-bottom: 1px solid $black;
    }
  }
  &__logo {
    position: absolute;
    width: em(90);
    height: em(90);
    right: em(50);
    top: em(-100);
    img {
      width: 100%;
    }
  }
  &--hide {
    transform: translateY(99%);
  }
}
@media screen and (max-width: $desktop-width) {
  .footer {
    padding: em(20);
    justify-content: flex-start;
    transform: translateY(-110px);
    p {
      text-align: left;
    }
    &__logo {
      top: em(20);
      width: em(65);
      height: em(65);
    }
    &--hide {
      transform: translateY(0);
    }
  }
}
