.home {
  display: flex;
  &__wrapper {
    width: calc(100% - em(170));
    height: 100%;
    max-width: em(1750);
    background: url('../../assets/pages/home/bg-home.png') no-repeat 50% 75%/110%;
  }

  &__entry {
    position: absolute;
    top: 39.7vw;
    left: 53.64vw;
    width: em(136);
    height: em(87);
    background: url('../../assets/pages/home/entry.png') no-repeat center/100%;
    animation: entry 1s alternate-reverse infinite;
  }
  .info {
    position: fixed;
    left: 5.4vw;
    // bottom: em(64);
    bottom: 0;
    width: em(440);
    height: em(360);
    border-radius: em(32);
    border: em(4) solid $black;
    background-color: $white;
    border-bottom: none;
    border-bottom-left-radius: 0;
    border-bottom-right-radius: 0;
    &__title {
      position: absolute;
      left: em(32);
      top: em(-32);
      display: inline-block;
      padding: em(20) em(32) em(25);
      background: url('../../assets/frames/title-yellow.png') no-repeat
        center/100%;
      p {
        color: $black;
        font-size: $f__24;
        font-weight: bold;
      }
    }
    &__navs {
      position: absolute;
      top: em(-60);
      display: flex;
      justify-content: start;
    }
    &__nav {
      display: flex;
      align-items: center;
      justify-content: center;
      margin-left: em(20);
      width: em(144);
      height: em(60);
      background: url('../../assets/pages/home/origin/nav.png') no-repeat
        center/100%;
      &--active {
        background: url('../../assets/pages/home/active/nav.png') no-repeat
          center/100%;
      }
      p {
        font-weight: bold;
        font-size: $f__24;
      }
    }
    &__container {
      z-index: 9;
      overflow-y: scroll;
      overflow-x: visible;
      position: relative;
      width: em(460);
      height: em(340);
      transform: translateX(-20px);
      padding: em(30) em(20) em(0) em(74);
    }
    &__link {
      position: relative;
      display: block;
      text-align: left;
      margin: em(40) 0;
      p {
        display: inline-block;
        position: relative;
        z-index: 9;
        font-size: $f__24;
        line-height: 2;
        font-weight: bold;
        &::before {
          z-index: -1;
          @include pseudo-element;
          width: 0%;
          height: em(10);
          top: em(12);
          left: 0;
          background-color: $yellow;
          transition: all 1s;
        }
      }
      span {
        display: block;
        font-size: $f__16;
      }
      &:hover {
        p {
          &::before {
            width: 100%;
          }
        }
        &::after {
          @include pseudo-element;
          top: 50%;
          left: em(-70);
          width: em(50);
          height: em(50);
          background: url('../../assets/buttons/btn-right-round.png') no-repeat
            center/100%;
          transform: translateY(-50%);
        }
      }
    }
  }
}

@media screen and (max-width: $laptop-width) {
  .home {
    font-size: 0.8em;
  }
}

@media screen and (max-width: $pad-width) {
  .home {
    font-size: 0.6em;
  }
}

@media screen and (max-width: $desktop-width) {
  .home {
    font-size: 1em;
    height: 100vh;
    &__wrapper {
      width: 100%;
      background: url('../../assets/pages/home/bg-home-m.png') no-repeat center -20%/100%;
    }
    &__entry {
      top: 72vw;
      left: 42vw;
    }
    .info {
      width: 80%;
      height: em(300);
      left: 10%;
      bottom: em(100);
      &__navs {
        width: 100%;
        top: em(-64);
        justify-content: center;
      }
      &__nav {
        width: em(188);
        height: em(64);
        margin-right: em(10);
        margin-left: em(10);
        background: url('../../assets/pages/home/origin/nav-m.png') no-repeat
          center/100%;
        &--active {
          background: url('../../assets/pages/home/active/nav-m.png') no-repeat
            center/100%;
        }
      }
      &__container {
        height: em(280);
        width: 100%;
      }
      &__link {
        margin: em(10) 0;
      }
    }
  }
}
